import {
  getChilds
} from './utils.js'

function initPagination () {
  const nums = document.querySelector('.number')
  const childs = getChilds(nums.childNodes)
  pagenationEvents(childs)
}

function pagenationEvents (childs) {
  const switchs = Array.from(document.querySelectorAll('.switch'))
  for (let i = 0, len = childs.length; i < len; i++) {
    const child = childs[i]
    child.addEventListener('click', function (e) {
      this.className = 'active'
      // 找到switch的index
      let switchIndex = 0
      for (let k = 0; k < switchs.length; k++) {
        if (switchs[k].className.search('active') !== -1) {
          switchIndex = k
          break
        }
      }
      switchs[switchIndex].querySelector('.list-wrap').style.transform = `translateX(-${i * 550}px)`
      for (let j = 0; j < childs.length; j++) {
        if (i !== j) { 
          childs[j].className = ''
        }
      }
    })
  }
}
initPagination()